@import '../common/variables.scss';
.background-foto {
  background: url('../images/back-mobile@1x.png') no-repeat 50% 0;
  background-size: cover;
  height: 463px;
  margin-top: -65px;
  position: relative;
}

.overflow {
  height: 65px;
  background: rgba(29,0,0,.302);
}

.background-foto__image {
  position: absolute;
  bottom: -3px;

  img {
    width: 100%;
  }
}

@media (min-width: $tablet-width) {
  .background-foto {
    background: url('../images/back-tablet@1x.png') no-repeat 50% 0;
    background-size: cover;
    height: 733px;
    margin-top: -110px;
  }

  .overflow {
    height: 110px;
    background: rgba(29,0,0,.302);
  }
}

@media (min-width: $desktop-width) {
  .background-foto {
    background: url('../images/back-desktop.png') no-repeat 50% 0;
    background-size: cover;
    height: 853px;
  }

  .background-foto__image {
    left: 15%;
  }
}