@import 'variables';

body {
  padding: 0;
  margin: 0;
  font-family: "OpenSans", "Arial", sans-serif;
  font-style: normal;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.none {
  display: none;
}

.block {
  display: block!important;
}

