@import '../common/variables.scss';

.price {
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url(./../images/fon.jpg);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  height: 476px;
}

.price__wrapper {
  width: 87%;
  padding-top: 5%;
}

.price__name {
  height: 100px;
  background-color: #283645;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.price__kind {
  font-size: 18px;
  color: rgb(255, 255, 255);
  font-weight: bold;
  text-transform: uppercase;
  line-height: 1.667;
}

.price__cost {
  font-size: 24px;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  line-height: 1.25;
}

.price__item--second > .price__name {
  position: relative;

  &::before {
    content: "";
    width: 74px;
    height: 73px;
    position: absolute;
    top: 0;
    left: 0;
    transform: rotate(0deg);
    text-align: center;
    background-image: url(./../images/hit.png);
    background-repeat: no-repeat;
  }

  &::after {
    content: "Хит";
    position: absolute;
    top: 12%;
    left: 2%;
    transform: rotate(-45deg);
    font-size: 14px;
    line-height: 30px;
    color: #fff;
    text-transform: uppercase;
  }
}

.price__item--first {

  .price__option--smile {
    background-image: url(./../images/no.png);
  }

  .price__option--comments {
    background-image: url(./../images/no.png);
  }
}

.price__item--second {

  .price__option--smile {
    background-image: url(./../images/yes.png);
  }

  .price__option--comments {
    background-image: url(./../images/no.png);
  }
}

.price__item--three {

  .price__option--smile {
    background-image: url(./../images/yes.png);
  }

  .price__option--comments {
    background-image: url(./../images/yes.png);
  }
}

.price__option {
  height: 60px;
  border: 2px solid #e5e5e5;
  background-color: #fff;
  font-size: 14px;
  color: rgb(40, 54, 69);
  text-transform: uppercase;
  line-height: 2.143;
  display: flex;
  align-items: center;
  padding-left: 5%;
  background-position-x: 90%;
  background-position-y: center;
  background-repeat: no-repeat;
}

.price__option--filter {
  background-image: url(./../images/yes.png);
}

.price__name {
  border: 2px solid #e5e5e5;
}

.price__options--tablet,
.price__name--tablet,
.price__item--tablet {
  display: none;
}
.price__list > .owl-dots {
  margin-top: 40px;
}


@media (min-width: $tablet-width) {

  .price__wrapper {
    width: 94%;
  }

  .price__list {
    display: flex;
    width: 100%;
  }

  .price__list > .item {
    width: 34%;
  }

  .price__options--tablet,
  .price__name--tablet,
  .price__item--tablet {
    display: flex;
    flex-direction: column;
  }

  .price__name--tablet {
    background-color: transparent;
    border: none;
    height: 104px;
  }

  .price__item--tablet {
    width: 33%;
  }

  .price__items {
    display: flex;
    align-items: center;
  }

  .price__item--first {

    .price__option--filter,
    .price__option--smile,
    .price__option--comments {
      font-size: 0;
      background-position-x: center;
      background-position-y: center;
    }
  }
  
  .price__item--second {

    .price__option--filter,
    .price__option--smile,
    .price__option--comments {
      font-size: 0;
      background-position-x: center;
      background-position-y: center;
    }
  }
  
  .price__item--three {

    .price__option--filter,
    .price__option--smile,
    .price__option--comments {
      font-size: 0;
      background-position-x: center;
      background-position-y: center;
    }
  }

  .price__options--tablet {

    .price__option--filter {
      background-image: none;
    }
  }

  .price__kind {
    font-size: 16px;
  }

  .price__cost {
    font-size: 18px;
  }

  .price__item--second > .price__name {
    &::after {
      top: 15%;
      left: 3%;
    }
  }
}

@media (min-width: $desktop-width) {
  .price__wrapper {
    width: 80%;
  }

  .price__kind {
    font-size: 18px;
  }

  .price__cost {
    font-size: 24px;
  }
}